.number-btns{margin:0 10% 0 10%;display:flex;justify-content:space-between;flex-wrap:wrap}.number-btn{font-size:2rem;color:white;width:20%;margin:5% 2.5% 5% 2.5%;border-radius:5px;box-shadow:inset 0 0 15px 0 grey;background-color:#212121;outline:none}.number-btn:disabled{color:grey}.onoff{padding:0;font-size:1.5rem;width:20%;height:40px;border-radius:5px;background-color:orange;color:#212121;align-self:flex-start;margin-left:11%;margin-right:10%;outline:none}.clear{background-color:lightgoldenrodyellow;color:#212121}.clear:disabled{color:grey}.onoff-solar{width:100%;display:flex;justify-content:space-between;align-items:center}.onoff-solar_title{width:50%;margin-right:11%;display:flex;border:2px solid black;border-radius:5px}.onoff-solar_title div{width:25%;padding:10% 0;background-color:darkslategrey;border-right:2px solid black;transition:all 0.3s ease-in-out}.onoff-solar_title div:hover{background-color:firebrick}.onoff-solar_title div:last-child{border-right:none}
