.display{font-family:'Cutive Mono', monospace;color:dimgray;font-weight:500;width:85%;height:100px;margin:5% 0;display:flex;justify-content:flex-end;align-items:center;box-sizing:border-box;border:2px solid black;border-radius:10px;box-shadow:inset 0 0 10px 0 black;position:relative}.display--on{background-color:lightgreen}.display--off{color:silver;background-color:lightgrey}.display__numbers{width:100%;margin:7.5% 2% 0 2%;display:flex;justify-content:flex-end;align-items:flex-start;overflow-x:hidden}.indicators{list-style-type:none;padding:0;margin:0;display:flex;justify-content:center;align-items:flex-start}.indicators__item{font-size:15px;font-weight:bold;padding:1px 4px;position:absolute;top:10%;border-radius:2px}.indicators__item--on{color:lightgreen;background-color:dimgray;opacity:0.6}.indicators__item--off{color:lightgrey;background-color:dimgray;opacity:0.1}.indicators__item--err-on{background-color:tomato;opacity:0.6}.indicators__item--err-off{background-color:dimgray;opacity:0.1}.indicators__item--total-on{opacity:0.6}.indicators__item--total-off{opacity:0.1}.indicators__res{left:15px}.indicators__sqrt{left:55px}.indicators__err{left:105px;color:lightgreen}.indicators__total{right:15px;color:dimgray;border:2px solid dimgray}
